.container{
    max-width: 60%;
    margin: 25px auto;
}
.headerName{
    text-align: center;
    color: blueviolet;
    font-weight: 800;
}
.addTodo{
    text-align: center;
    display: -ms-flexbox;
    display: flex;
    padding: 12px;
    background-color: rgb(133, 102, 146);

}
input{
   width: 70%;
   font-size: 28px;
    padding: 5px;
    border:none;
}
.addTodoBtn{
    width: 30%;
    color: blue;
    font-size: 28px;
    background-color:rgb(66, 172, 66);
    cursor: pointer;
    padding: 5px;
    border: none;
}

button:disabled{
    cursor: default;
}
.resetButton{
    width: 100%;
    cursor: pointer;
    border: none;
    background-color: #a5afd7;
    font-size: 32px;
    color:#261834;
    padding: 10px;
    border-bottom:4px solid #956cbc;
}
.todos{
    border-bottom: 2px solid #956cbc;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    background-color: #bfa6d6;
    font-size: 22px;
    color:white;

}

.removeBtn{
    border: none;
    background-color: #bfa6d6;
    color: white;
    font-size: 22px;
    cursor: pointer;
    width: 25%;
}
.todoText{
    padding: 10px;
    
}
.footer{
    text-align: center;
  margin: 60px;
    font-size: 18px;
    color: red;
    
}


